import { initializeApp } from 'firebase/app';
import {
  child,
  getDatabase,
  onValue,
  push,
  ref,
  update,
} from 'firebase/database';
import { createContext, PropsWithChildren, useCallback } from 'react';
import config from './config';

interface Controller {
  addItem: (item: any) => void;
  getChecklist: (cd: any) => void;
}
export const Context = createContext<Controller>({} as Controller);
const { Provider } = Context;

export default function FirebaseProvider({ children }: PropsWithChildren) {
  initializeApp(config);
  const db = getDatabase();
  const checklistRef = ref(db, '/quests/0/steps');

  const getChecklist = useCallback(
    async (cb: any) => {
      // return new Promise((resolve) => {
      onValue(checklistRef, (snapshot) => {
        const data = snapshot.val();
        return cb(Object.entries(data).map(([key, value]) => ({ key, value })));
      });
      // });
    },
    [checklistRef]
  );

  const addItem = (item: string) => {
    const newPostKey = push(child(ref(db), '/quests/0/steps')).key;
    update(ref(db, '/quests/0/steps'), { [`/${newPostKey}`]: item });
  };

  const controller = { addItem, getChecklist };
  return <Provider value={controller}>{children}</Provider>;
}
