/* eslint-disable react/jsx-props-no-spreading */
import { useContext, useEffect, useState } from 'react';

import Box from '@mui/material/Box';
import Checkbox from '@mui/material/Checkbox';
import IconButton from '@mui/material/IconButton';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import CommentIcon from '@mui/icons-material/Edit';
import Button from '@mui/material/Button';
import Input from '@mui/material/Input';
import {
  DragDropContext,
  Draggable,
  Droppable,
  DropResult,
} from 'react-beautiful-dnd';
import { FirebaseContext } from '../firebase';

export default function Quest() {
  const { addItem, getChecklist } = useContext(FirebaseContext);
  const [checked, setChecked] = useState([0]);

  const [item, setItem] = useState('');
  const [data, setData] = useState([]);

  const handleSubmit = (e: any) => {
    e.preventDefault();
    addItem({ title: item, order: data.length });
    setItem('');
  };

  const handleChange = (e: any) => {
    if (e.key === 'Enter') {
      handleSubmit(e);
      return;
    }
    setItem(e.target.value);
  };

  useEffect(() => {
    getChecklist(setData);
  }, [getChecklist]);

  const handleToggle = (value: number) => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    setChecked(newChecked);
  };

  const onDragEnd = ({ destination, source }: DropResult) => {
    console.log(destination, source, data);
    // dropped outside the list
    // if (!destination) return;

    // const newItems = reorder(items, source.index, destination.index);

    // setItems(newItems);
  };

  return (
    <Box component="main" sx={{ flexGrow: 1, p: 3 }}>
      <DragDropContext onDragEnd={onDragEnd}>
        <Droppable droppableId="droppable-list">
          {(provided) => (
            <List
              sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}
              ref={provided.innerRef}
              {...provided.droppableProps}
            >
              {data.map(({ key, value: { title } }, index) => {
                const labelId = `checkbox-list-label-${title}`;

                return (
                  <Draggable draggableId={key} index={index}>
                    {(provided$) => (
                      <ListItem
                        ref={provided$.innerRef}
                        {...provided$.draggableProps}
                        {...provided$.dragHandleProps}
                        key={key}
                        secondaryAction={
                          <IconButton edge="end" aria-label="comments">
                            <CommentIcon />
                          </IconButton>
                        }
                        disablePadding
                      >
                        <ListItemButton
                          role={undefined}
                          onClick={handleToggle(title)}
                          dense
                        >
                          <ListItemIcon>
                            <Checkbox
                              edge="start"
                              checked={checked.indexOf(title) !== -1}
                              tabIndex={-1}
                              disableRipple
                              inputProps={{ 'aria-labelledby': labelId }}
                            />
                          </ListItemIcon>
                          <ListItemText id={labelId} primary={title} />
                        </ListItemButton>
                      </ListItem>
                    )}
                  </Draggable>
                );
              })}
              {provided.placeholder}
            </List>
          )}
        </Droppable>
      </DragDropContext>
      <ListItem>
        <form onSubmit={handleSubmit}>
          <Input value={item} onChange={handleChange} />
          <Button sx={{ fontWeight: 700 }} type="submit">
            Add new step
          </Button>
        </form>
      </ListItem>
    </Box>
  );
}
